.ArticleContainer
{
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 1000;
    display: none;
}
.ArticleContainer.show
{
    display: inherit;
}
.Article{
        background: none;
        color: black;
        border: none;
        padding: 0;
        font: inherit;
        outline: none;
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 80%;
        height: 80%;
        background-color: white;
        position: fixed;
        top: 10%;
        left: 10%;
}
.Article h2{
    text-align: center;
}