.webSiteNavBar.navbar{
    position: fixed;
    top: 0;
    width: 100%;
}
button.menuButton{
    all: unset;
}
button.menuButton:hover {
    box-shadow: 0 0 10px 5px black;
}
button.menuButton:click {
    border: none;
}
.navbar
{
    position: fixed;
    top: 0;
    direction: rtl;
}
.darkDropDown .dropdown-menu.show
{
    background-color: #212529;
}

.navbar .nav {
    display: block;
    float: right;
    left: 0;
    margin: 0 10px 0 0;
    position: relative;
}
.nav-link
{
    text-align: right;
}


.navbar-brand {
    color: #777777;
    display: block;
    float: left;
    font-size: 20px;
    font-weight: 200;
    margin-left: -20px;
    padding: 10px 20px;
    text-shadow: 0 1px 0 #FFFFFF;
}


.navbar .nav > li {
    float: right;
}